// Fix y-prosemirror unusable in a Shadow DOM.
ShadowRoot.prototype['createRange'] = document.createRange.bind(document);

// Hide monaco editor aria container.
document.head.insertAdjacentHTML('beforeend', `<style> body > div.monaco-aria-container {display: none;} </style>`);

// Inject codicons stylesheet for monaco editor, since font cannot be loaded in Shadow DOM.
const currentScriptURL = performance.getEntriesByType('resource').slice(-1)[0].name;
document.head.insertAdjacentHTML('beforeend', `<link href="${currentScriptURL.replace(/(.*\/).*?$/, '$1')}codicon.css?t=${Date.now()}" rel="stylesheet" />`);
